import React, { useState, useEffect, useContext } from "react";
import axios from "axios";
import { useParams, useNavigate } from "react-router-dom";
import { AuthContext } from "@/App";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion";
import { toast } from "sonner";
import { ArrowLeft, Star, Trash2, Bookmark, BookmarkCheck } from "lucide-react";

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;
const TMDB_IMAGE_BASE = "https://image.tmdb.org/t/p/original";

const ContentDetailPage = () => {
  const { id } = useParams();
  const { user } = useContext(AuthContext);
  const navigate = useNavigate();
  const [content, setContent] = useState(null);
  const [reviews, setReviews] = useState([]);
  const [rating, setRating] = useState(0);
  const [reviewText, setReviewText] = useState("");
  const [loading, setLoading] = useState(true);
  const [inWatchlist, setInWatchlist] = useState(false);

  useEffect(() => {
    fetchContent();
    fetchReviews();
    if (user) {
      checkWatchlist();
    }
  }, [id, user]);

  const fetchContent = async () => {
    try {
      const response = await axios.get(`${API}/content/${id}`);
      setContent(response.data);
    } catch (error) {
      console.error("Error fetching content", error);
      toast.error("Error al cargar el contenido");
    } finally {
      setLoading(false);
    }
  };

  const fetchReviews = async () => {
    try {
      const response = await axios.get(`${API}/reviews/content/${id}`);
      setReviews(response.data);
    } catch (error) {
      console.error("Error fetching reviews", error);
    }
  };

  const checkWatchlist = async () => {
    try {
      const response = await axios.get(`${API}/watchlist/check/${id}`);
      setInWatchlist(response.data.in_watchlist);
    } catch (error) {
      console.error("Error checking watchlist", error);
    }
  };

  const toggleWatchlist = async () => {
    if (!user) {
      toast.error("Debes iniciar sesin");
      return;
    }

    try {
      if (inWatchlist) {
        await axios.delete(`${API}/watchlist/${id}`);
        toast.success("Eliminado de ver luego");
        setInWatchlist(false);
      } else {
        await axios.post(`${API}/watchlist/${id}`);
        toast.success("Agregado para ver luego");
        setInWatchlist(true);
      }
    } catch (error) {
      toast.error(error.response?.data?.detail || "Error al actualizar lista");
    }
  };

  const handleSubmitReview = async () => {
    if (!user) {
      toast.error("Debes iniciar sesin para dejar una resea");
      return;
    }

    if (rating === 0 || !reviewText.trim()) {
      toast.error("Por favor completa todos los campos");
      return;
    }

    try {
      await axios.post(`${API}/reviews`, {
        content_id: id,
        rating,
        review_text: reviewText,
      });
      toast.success("Resea enviada exitosamente");
      setRating(0);
      setReviewText("");
      fetchReviews();
    } catch (error) {
      toast.error(error.response?.data?.detail || "Error al enviar la resea");
    }
  };

  const handleDeleteReview = async (reviewId) => {
    try {
      await axios.delete(`${API}/reviews/${reviewId}`);
      toast.success("Resea eliminada");
      fetchReviews();
    } catch (error) {
      toast.error("Error al eliminar la resea");
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900">
        <div className="text-white text-xl">Cargando...</div>
      </div>
    );
  }

  if (!content) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900">
        <div className="text-white text-xl">Contenido no encontrado</div>
      </div>
    );
  }

  const backdropUrl = content.backdrop_path
    ? `${TMDB_IMAGE_BASE}${content.backdrop_path}`
    : null;
  const posterUrl = content.poster_path
    ? `${TMDB_IMAGE_BASE}${content.poster_path}`
    : "https://via.placeholder.com/500x750?text=No+Image";

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900">
        {/* Hero Section with Backdrop */}
        <div className="relative h-[40vh] sm:h-[60vh] overflow-hidden">
        {backdropUrl && (
          <>
            <img
              src={backdropUrl}
              alt={content.title}
              className="w-full h-full object-cover object-center"
            />
            <div className="absolute inset-0 bg-gradient-to-t from-slate-900 via-slate-900/60 to-transparent" />
          </>
        )}
        <Button
          data-testid="back-button"
          onClick={() => navigate("/")}
          variant="ghost"
          className="absolute top-4 left-4 text-white hover:bg-white/10"
        >
          <ArrowLeft className="w-5 h-5 mr-2" />
          Volver
        </Button>
      </div>

      {/* Content Details */}
      <div className="container mx-auto px-4 -mt-20 md:-mt-32 relative z-10 pb-16">
        <div className="flex flex-col md:flex-row gap-6">
          {/* Poster */}
          <div className="w-full md:w-80 flex-none flex justify-center md:justify-start self-start">
            <img
              src={posterUrl}
              alt={content.title}
              className="w-48 sm:w-56 md:w-80 rounded-2xl shadow-2xl object-contain h-auto max-h-[560px] md:max-h-[520px]"
            />
          </div>

          {/* Info */}
          <div className="flex-1 space-y-4">
            <div>
              <h1 className="text-2xl sm:text-4xl md:text-5xl font-bold mb-1">{content.title}</h1>
              {content.original_title && content.original_title !== content.title && (
                <p className="text-xl text-gray-400 italic">({content.original_title})</p>
              )}
            </div>

            {user && (
              <div className="flex flex-col md:flex-row md:items-center md:gap-4">
                <Button
                  data-testid="watchlist-button"
                  onClick={toggleWatchlist}
                  className={`w-full md:w-auto py-2 ${
                    inWatchlist
                      ? "bg-gradient-to-r from-green-500 to-emerald-500 hover:from-green-600 hover:to-emerald-600"
                      : "bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600"
                  }`}
                >
                  {inWatchlist ? (
                    <>
                      <BookmarkCheck className="w-5 h-5 mr-2 inline-block" />
                      <span className="align-middle">En mi lista</span>
                    </>
                  ) : (
                    <>
                      <Bookmark className="w-5 h-5 mr-2 inline-block" />
                      <span className="align-middle">Ver luego</span>
                    </>
                  )}
                </Button>
              </div>
            )}

            <div className="flex flex-wrap gap-2">
              {content.genres.map((genre, idx) => (
                <span
                  key={idx}
                  className="px-2 py-1 bg-cyan-500/20 text-cyan-300 rounded-full text-xs border border-cyan-500/30"
                >
                  {genre}
                </span>
              ))}
            </div>

            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
              {content.vote_average && (
                <div className="glass rounded-xl p-3 sm:p-4 md:p-6">
                  <p className="text-gray-400 text-sm">Calificacin</p>
                  <p className="text-2xl font-bold text-yellow-400"> {content.vote_average.toFixed(1)}</p>
                </div>
              )}
              {content.release_date && (
                <div className="glass rounded-xl p-3 sm:p-4 md:p-6">
                  <p className="text-gray-400 text-sm">Fecha de Lanzamiento</p>
                  <p className="text-xl font-semibold">{content.release_date}</p>
                </div>
              )}
            </div>

            {content.countries && content.countries.length > 0 && (
              <div className="glass rounded-xl p-3 sm:p-4 md:p-6">
                <p className="text-gray-400 text-sm mb-2">Pas</p>
                <p className="text-lg">{content.countries.join(", ")}</p>
              </div>
            )}

            {content.overview && (
              <div className="glass rounded-xl p-4 sm:p-6 md:p-6">
                <h3 className="text-lg sm:text-xl md:text-xl font-bold mb-2">Sinopsis</h3>
                <p className="text-gray-300 leading-relaxed text-sm sm:text-base">{content.overview}</p>
              </div>
            )}

            {/* Seasons & Episodes */}
            {content.seasons && content.seasons.length > 0 && (
              <div className="glass rounded-xl p-4 sm:p-6 md:p-6">
                <h3 className="text-xl font-bold mb-4">Temporadas y Episodios</h3>
                <Accordion type="single" collapsible className="space-y-2">
                  {content.seasons.map((season) => (
                    <AccordionItem
                      key={season.season_number}
                      value={`season-${season.season_number}`}
                      className="border-white/10"
                    >
                      <AccordionTrigger className="hover:no-underline hover:bg-white/5 px-4 rounded-lg">
                        <span className="font-semibold">
                          {season.name} - {season.episode_count} episodios
                        </span>
                      </AccordionTrigger>
                      <AccordionContent className="px-4 pt-4">
                        <div className="space-y-3">
                          {season.episodes && season.episodes.map((episode) => (
                            <div
                              key={episode.episode_number}
                                      className="bg-white/5 rounded-lg p-3 sm:p-4 border border-white/10"
                            >
                              <p className="font-semibold">
                                Episodio {episode.episode_number}: {episode.name}
                              </p>
                              {episode.overview && (
                                <p className="text-sm text-gray-400 mt-1">{episode.overview}</p>
                              )}
                            </div>
                          ))}
                        </div>
                      </AccordionContent>
                    </AccordionItem>
                  ))}
                </Accordion>
              </div>
            )}
          </div>
        </div>

        {/* Reviews Section */}
        <div className="mt-12">
          <h2 className="text-3xl font-bold mb-8">Reseas</h2>

          {/* Add Review */}
          {user && (
            <Card className="bg-slate-800/50 border-white/10 mb-8">
              <CardHeader>
                <CardTitle className="text-white">Deja tu Resea</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <p className="text-sm text-gray-400 mb-2">Calificacin</p>
                  <div className="flex gap-2">
                    {[1, 2, 3, 4, 5].map((star) => (
                      <button
                        key={star}
                        data-testid={`rating-star-${star}`}
                        onClick={() => setRating(star)}
                        className="transition-transform hover:scale-105 p-1"
                      >
                        <Star
                          className={`w-6 md:w-8 h-6 md:h-8 ${
                            star <= rating
                              ? "fill-yellow-400 text-yellow-400"
                              : "text-gray-600"
                          }`}
                        />
                      </button>
                    ))}
                  </div>
                </div>
                <Textarea
                  data-testid="review-text-input"
                  placeholder="Escribe tu resea aqu..."
                  value={reviewText}
                  onChange={(e) => setReviewText(e.target.value)}
                  className="bg-slate-900/50 border-white/10 text-white min-h-[120px]"
                />
                <Button
                  data-testid="submit-review-button"
                  onClick={handleSubmitReview}
                  className="w-full md:w-auto bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600"
                >
                  Enviar Resea
                </Button>
              </CardContent>
            </Card>
          )}

          {/* Reviews List */}
          <div className="space-y-4">
            {reviews.length === 0 ? (
              <p className="text-gray-400 text-center py-8">No hay reseas todava</p>
            ) : (
              reviews.map((review) => (
                <Card key={review.id} className="bg-slate-800/50 border-white/10">
                  <CardContent className="pt-6">
                    <div className="flex justify-between items-start mb-3">
                      <div>
                        <p className="font-semibold text-white">{review.user_email}</p>
                        <div className="flex gap-1 mt-1">
                          {[...Array(5)].map((_, idx) => (
                            <Star
                              key={idx}
                              className={`w-4 h-4 ${
                                idx < review.rating
                                  ? "fill-yellow-400 text-yellow-400"
                                  : "text-gray-600"
                              }`}
                            />
                          ))}
                        </div>
                      </div>
                      {(user?.role === "admin" || user?.email === review.user_email) && (
                        <Button
                          data-testid={`delete-review-${review.id}`}
                          onClick={() => handleDeleteReview(review.id)}
                          variant="ghost"
                          size="sm"
                          className="text-red-400 hover:text-red-300 hover:bg-red-500/10"
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      )}
                    </div>
                    <p className="text-gray-300">{review.review_text}</p>
                    <p className="text-xs text-gray-500 mt-2">
                      {new Date(review.created_at).toLocaleDateString()}
                    </p>
                  </CardContent>
                </Card>
              ))
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ContentDetailPage;