import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import { getCache } from "@/lib/cache";
import ContentCard from "@/components/ContentCard";

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL || 'http://localhost:8000';
const API = `${BACKEND_URL}/api`;

const CategoryPage = () => {
  const { type } = useParams();
  const [items, setItems] = useState([]);

  useEffect(() => {
    const fetch = async () => {
      try {
        const res = await axios.get(`${API}/content`);
        const data = res.data || [];
        const filtered = data
          .filter((c) => c.content_type === type)
          .sort((a, b) => new Date(b.created_at) - new Date(a.created_at));
        setItems(filtered);
        // update cache for offline fallback
        try { localStorage.setItem('showlib_content', JSON.stringify(data)); } catch {}
      } catch (err) {
        const cached = getCache("content");
        if (cached) {
          const filtered = cached
            .filter((c) => c.content_type === type)
            .sort((a, b) => new Date(b.created_at) - new Date(a.created_at));
          setItems(filtered);
          return;
        }
        console.error(err);
      }
    };
    fetch();
  }, [type]);

  return (
    <div className="container mx-auto px-4 py-8 pt-28">
      <h2 className="text-3xl font-bold mb-6">{type === 'movie' ? 'Pelculas' : type === 'tv' ? 'Series' : 'Novelas'}</h2>
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
        {items.map((item) => (
          <ContentCard key={item.id} content={item} />
        ))}
      </div>
    </div>
  );
};

export default CategoryPage;
