import React, { useState, useEffect } from "react";
import "@/App.css";
import { BrowserRouter, Routes, Route, Navigate } from "react-router-dom";
import axios from "axios";
import { Toaster } from "@/components/ui/sonner";
import { toast } from "sonner";

import HomePage from "@/pages/HomePage";
import ContentDetailPage from "@/pages/ContentDetailPage";
import AdminPanel from "@/pages/AdminPanel";
import UserProfile from "@/pages/UserProfile";
import CategoryPage from "@/pages/CategoryPage";

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL || 'http://localhost:8000';
const API = `${BACKEND_URL}/api`;

import AuthContextModule from "@/contexts/AuthContext";
// Support existing imports that expect a named export from '@/App'
export const AuthContext = AuthContextModule;
import Header from "@/components/Header";
import AuthModal from "@/components/AuthModal";

function App() {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [authOpen, setAuthOpen] = useState(false);

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (token) {
      axios.defaults.headers.common["Authorization"] = `Bearer ${token}`;
      fetchCurrentUser();
    } else {
      setLoading(false);
    }
  }, []);

  const fetchCurrentUser = async () => {
    try {
      const response = await axios.get(`${API}/auth/me`);
      setUser(response.data);
    } catch (error) {
      console.error("Failed to fetch user", error);
      localStorage.removeItem("token");
      delete axios.defaults.headers.common["Authorization"];
    } finally {
      setLoading(false);
    }
  };

  const login = (token, userData) => {
    localStorage.setItem("token", token);
    axios.defaults.headers.common["Authorization"] = `Bearer ${token}`;
    setUser(userData);
  };

  const logout = () => {
    localStorage.removeItem("token");
    delete axios.defaults.headers.common["Authorization"];
    setUser(null);
    toast.success("Sesin cerrada exitosamente");
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900">
        <div className="text-white text-xl">Cargando...</div>
      </div>
    );
  }


  return (
    <AuthContext.Provider value={{ user, login, logout }}>
      <div className="App">
        <BrowserRouter>
          <Header onRequestAuth={() => setAuthOpen(true)} />
          <Routes>
            <Route path="/" element={<HomePage />} />
            <Route path="/content/:id" element={<ContentDetailPage />} />
            <Route path="/category/:type" element={<CategoryPage />} />
            <Route
              path="/profile"
              element={
                user ? <UserProfile /> : <Navigate to="/" replace />
              }
            />
            <Route
              path="/admin"
              element={
                user?.role === "admin" ? (
                  <AdminPanel />
                ) : (
                  <Navigate to="/" replace />
                )
              }
            />
          </Routes>
          <AuthModal open={authOpen} onOpenChange={setAuthOpen} />
        </BrowserRouter>
        <Toaster position="top-right" />
      </div>
    </AuthContext.Provider>
  );
}

export default App;