import React, { useState, useEffect, useContext } from "react";
import axios from "axios";
import { AuthContext } from "@/App";
import { useNavigate } from "react-router-dom";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { toast } from "sonner";
import { ArrowLeft, Plus, Trash2, Users, Film, MessageSquare } from "lucide-react";

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;

const AdminPanel = () => {
  const { user } = useContext(AuthContext);
  const navigate = useNavigate();
  const [users, setUsers] = useState([]);
  const [content, setContent] = useState([]);
  const [reviews, setReviews] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [contentType, setContentType] = useState("movie");
  const [searchResults, setSearchResults] = useState([]);
  const [searching, setSearching] = useState(false);

  useEffect(() => {
    if (!user || user.role !== "admin") {
      navigate("/");
      return;
    }
    fetchUsers();
    fetchContent();
    fetchReviews();
  }, [user]);

  const fetchUsers = async () => {
    try {
      const response = await axios.get(`${API}/admin/users`);
      setUsers(response.data);
      try { localStorage.setItem('admin_users', JSON.stringify(response.data)); } catch {}
    } catch (error) {
      console.error("Error fetching users", error);
      try {
        const raw = localStorage.getItem('admin_users');
        if (raw) setUsers(JSON.parse(raw));
      } catch {}
    }
  };

  const fetchContent = async () => {
    try {
      const response = await axios.get(`${API}/content`);
      setContent(response.data);
      try { localStorage.setItem('showlib_content', JSON.stringify(response.data)); } catch {}
    } catch (error) {
      console.error("Error fetching content", error);
      try {
        const raw = localStorage.getItem('showlib_content');
        if (raw) setContent(JSON.parse(raw));
      } catch {}
    }
  };

  const fetchReviews = async () => {
    try {
      const response = await axios.get(`${API}/admin/reviews`);
      setReviews(response.data);
      try { localStorage.setItem('admin_reviews', JSON.stringify(response.data)); } catch {}
    } catch (error) {
      console.error("Error fetching reviews", error);
      try {
        const raw = localStorage.getItem('admin_reviews');
        if (raw) setReviews(JSON.parse(raw));
      } catch {}
    }
  };

  const handleSearchContent = async () => {
    if (!searchQuery.trim()) {
      toast.error("Por favor ingresa un nombre para buscar");
      return;
    }

    setSearching(true);
    try {
      const response = await axios.post(`${API}/content/search`, {
        query: searchQuery,
        content_type: contentType,
      });
      setSearchResults(response.data.results);
      if (response.data.results.length === 0) {
        toast.info("No se encontraron resultados");
      }
    } catch (error) {
      toast.error("Error al buscar contenido");
    } finally {
      setSearching(false);
    }
  };

  const handleAddContent = async (tmdbId) => {
    try {
      await axios.post(`${API}/content`, {
        tmdb_id: tmdbId,
        content_type: contentType,
      });
      toast.success("Contenido agregado exitosamente");
      setSearchQuery("");
      setSearchResults([]);
      fetchContent();
    } catch (error) {
      toast.error(error.response?.data?.detail || "Error al agregar contenido");
    }
  };

  const handleDeleteContent = async (contentId) => {
    try {
      await axios.delete(`${API}/content/${contentId}`);
      toast.success("Contenido eliminado");
      fetchContent();
    } catch (error) {
      toast.error("Error al eliminar contenido");
    }
  };

  const handleUpdateUserRole = async (userId, newRole) => {
    try {
      await axios.patch(`${API}/admin/users/${userId}/role?role=${newRole}`);
      toast.success("Rol actualizado");
      fetchUsers();
    } catch (error) {
      toast.error("Error al actualizar rol");
    }
  };

  const handleDeleteUser = async (userId) => {
    try {
      await axios.delete(`${API}/admin/users/${userId}`);
      toast.success("Usuario eliminado");
      fetchUsers();
    } catch (error) {
      toast.error("Error al eliminar usuario");
    }
  };

  const handleDeleteReview = async (reviewId) => {
    try {
      await axios.delete(`${API}/reviews/${reviewId}`);
      toast.success("Resea eliminada");
      fetchReviews();
    } catch (error) {
      toast.error("Error al eliminar resea");
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900 p-8">
  <div className="container mx-auto mt-[43px]">
        <div className="flex items-center justify-between mb-8">
          <h1 className="text-4xl font-bold bg-gradient-to-r from-purple-400 to-pink-400 bg-clip-text text-transparent">
            Panel de Administracin
          </h1>
          <Button
            data-testid="back-to-home-button"
            onClick={() => navigate("/")}
            variant="outline"
            className="border-white/20 text-white hover:bg-white/10"
          >
            <ArrowLeft className="w-4 h-4 mr-2" />
            Volver al Inicio
          </Button>
        </div>

        <Tabs defaultValue="content" className="space-y-6">
          <TabsList className="bg-white/5 border border-white/10">
            <TabsTrigger value="content" data-testid="content-tab">
              <Film className="w-4 h-4 mr-2" />
              Contenido
            </TabsTrigger>
            <TabsTrigger value="users" data-testid="users-tab">
              <Users className="w-4 h-4 mr-2" />
              Usuarios
            </TabsTrigger>
            <TabsTrigger value="reviews" data-testid="reviews-tab">
              <MessageSquare className="w-4 h-4 mr-2" />
              Reseas
            </TabsTrigger>
          </TabsList>

          {/* Content Tab */}
          <TabsContent value="content">
            <Card className="bg-slate-800/50 border-white/10 mb-6">
              <CardHeader>
                <CardTitle className="text-white">Buscar y Agregar Nuevo Contenido</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <div className="flex gap-4">
                    <Input
                      data-testid="content-search-input"
                      type="text"
                      placeholder="Buscar por nombre (ej: Breaking Bad)"
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                      onKeyPress={(e) => e.key === "Enter" && handleSearchContent()}
                      className="bg-slate-900/50 border-white/10 text-white flex-1"
                    />
                    <Select value={contentType} onValueChange={setContentType}>
                      <SelectTrigger data-testid="content-type-select" className="w-40 bg-slate-900/50 border-white/10 text-white">
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent className="bg-slate-900 border-white/10">
                        <SelectItem value="movie">Pelcula</SelectItem>
                        <SelectItem value="tv">Serie</SelectItem>
                        <SelectItem value="novela">Novela</SelectItem>
                      </SelectContent>
                    </Select>
                    <Button
                      data-testid="search-content-button"
                      onClick={handleSearchContent}
                      disabled={searching}
                      className="bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600"
                    >
                      {searching ? "Buscando..." : "Buscar"}
                    </Button>
                  </div>

                  {/* Search Results */}
                  {searchResults.length > 0 && (
                    <div className="max-h-96 overflow-y-auto space-y-2">
                      <p className="text-sm text-gray-400 mb-2">Resultados de bsqueda:</p>
                      {searchResults.map((result) => (
                        <div
                          key={result.id}
                          className="flex items-center justify-between p-3 bg-white/5 rounded-lg border border-white/10"
                        >
                          <div className="flex items-center gap-3">
                            {result.poster_path && (
                              <img
                                src={`https://image.tmdb.org/t/p/w92${result.poster_path}`}
                                alt={result.title || result.name}
                                className="w-12 h-16 object-cover rounded"
                              />
                            )}
                            <div>
                              <p className="font-semibold text-white">
                                {result.title || result.name}
                              </p>
                              <p className="text-sm text-gray-400">
                                {result.release_date || result.first_air_date} -  {result.vote_average?.toFixed(1)}
                              </p>
                            </div>
                          </div>
                          <Button
                            data-testid={`add-result-${result.id}`}
                            onClick={() => handleAddContent(result.id)}
                            size="sm"
                            className="bg-gradient-to-r from-green-500 to-emerald-500 hover:from-green-600 hover:to-emerald-600"
                          >
                            <Plus className="w-4 h-4 mr-2" />
                            Agregar
                          </Button>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>

            <Card className="bg-slate-800/50 border-white/10">
              <CardHeader>
                <CardTitle className="text-white">Contenido Existente ({content.length})</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {content.map((item) => (
                    <div
                      key={item.id}
                      className="flex items-center justify-between p-4 bg-white/5 rounded-lg border border-white/10"
                    >
                      <div>
                        <p className="font-semibold text-white">{item.title}</p>
                        <p className="text-sm text-gray-400">
                          {item.content_type} - TMDB ID: {item.tmdb_id}
                        </p>
                      </div>
                      <Button
                        data-testid={`delete-content-${item.id}`}
                        onClick={() => handleDeleteContent(item.id)}
                        variant="ghost"
                        size="sm"
                        className="text-red-400 hover:text-red-300 hover:bg-red-500/10"
                      >
                        <Trash2 className="w-4 h-4" />
                      </Button>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Users Tab */}
          <TabsContent value="users">
            <Card className="bg-slate-800/50 border-white/10">
              <CardHeader>
                <CardTitle className="text-white">Usuarios ({users.length})</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {users.map((u) => (
                    <div
                      key={u.id}
                      className="flex items-center justify-between p-4 bg-white/5 rounded-lg border border-white/10"
                    >
                      <div>
                        <p className="font-semibold text-white">{u.email}</p>
                        <p className="text-sm text-gray-400">Rol: {u.role}</p>
                      </div>
                      <div className="flex gap-2">
                        <Select
                          value={u.role}
                          onValueChange={(newRole) => handleUpdateUserRole(u.id, newRole)}
                        >
                          <SelectTrigger data-testid={`user-role-select-${u.id}`} className="w-32 bg-slate-900/50 border-white/10 text-white">
                            <SelectValue />
                          </SelectTrigger>
                          <SelectContent className="bg-slate-900 border-white/10">
                            <SelectItem value="user">Usuario</SelectItem>
                            <SelectItem value="admin">Admin</SelectItem>
                          </SelectContent>
                        </Select>
                        <Button
                          data-testid={`delete-user-${u.id}`}
                          onClick={() => handleDeleteUser(u.id)}
                          variant="ghost"
                          size="sm"
                          className="text-red-400 hover:text-red-300 hover:bg-red-500/10"
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Reviews Tab */}
          <TabsContent value="reviews">
            <Card className="bg-slate-800/50 border-white/10">
              <CardHeader>
                <CardTitle className="text-white">Reseas ({reviews.length})</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {reviews.map((review) => (
                    <div
                      key={review.id}
                      className="flex items-start justify-between p-4 bg-white/5 rounded-lg border border-white/10"
                    >
                      <div className="flex-1">
                        <p className="font-semibold text-white">{review.user_email}</p>
                        <p className="text-sm text-yellow-400 mb-2"> {review.rating}/5</p>
                        <p className="text-gray-300">{review.review_text}</p>
                      </div>
                      <Button
                        data-testid={`admin-delete-review-${review.id}`}
                        onClick={() => handleDeleteReview(review.id)}
                        variant="ghost"
                        size="sm"
                        className="text-red-400 hover:text-red-300 hover:bg-red-500/10"
                      >
                        <Trash2 className="w-4 h-4" />
                      </Button>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
};

export default AdminPanel;