const PREFIX = "fastcopy_cache_v1:";

function safeParse(str) {
  try {
    return JSON.parse(str);
  } catch {
    return null;
  }
}

export function setCache(key, value, ttlSeconds = 24 * 3600) {
  const payload = { v: value, t: Date.now() + ttlSeconds * 1000 };
  try {
    localStorage.setItem(PREFIX + key, JSON.stringify(payload));
  } catch (e) {
    // ignore quota errors
  }
}

export function getCache(key) {
  try {
    const raw = localStorage.getItem(PREFIX + key);
    if (!raw) return null;
    const parsed = safeParse(raw);
    if (!parsed) return null;
    if (parsed.t && Date.now() > parsed.t) {
      localStorage.removeItem(PREFIX + key);
      return null;
    }
    return parsed.v;
  } catch (e) {
    return null;
  }
}

export function clearCache(key) {
  try {
    localStorage.removeItem(PREFIX + key);
  } catch {}
}

export default { setCache, getCache, clearCache };
