import React, { useContext, useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { LogIn, LogOut, User, Shield, Search } from "lucide-react";
import { AuthContext } from "@/contexts/AuthContext";

const Header = ({ onRequestAuth }) => {
  const { user, logout } = useContext(AuthContext);
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    // If ?auth=login in URL, open modal
    const params = new URLSearchParams(location.search);
    if (params.get("auth") === "login") {
      onRequestAuth && onRequestAuth(true);
    }
  }, [location.search, onRequestAuth]);

  return (
    <header className="fixed top-0 left-0 right-0 z-50 glass">
      <div className="container mx-auto px-4 py-2 flex items-center justify-between">
        <div className="flex items-center space-x-2 cursor-pointer" onClick={() => navigate('/') }>
          <img src={require("@/assets/Logo FastCopy HD.png")} alt="FastCopy Logo" className="w-8 h-8 object-contain" />
          <h1 className="text-lg font-bold bg-gradient-to-r from-cyan-400 to-blue-500 bg-clip-text text-transparent">FastCopy</h1>
        </div>

        <div className="flex items-center gap-1 sm:gap-2">
          {user && (
            <div className="relative hidden sm:block">
              <Search className="absolute left-2 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
              <Input type="text" placeholder="Buscar..." className="pl-8 w-32 lg:w-48 h-8 bg-white/10 border-white/20 text-white placeholder:text-gray-400 text-sm" />
            </div>
          )}

          {user ? (
            <>
              {user.role === "admin" && (
                <Button size="sm" onClick={() => navigate("/admin")} className="bg-gradient-to-r from-purple-500 to-pink-500 hover:from-purple-600 hover:to-pink-600 h-8 px-2 text-sm">
                  <Shield className="w-4 h-4" />
                  <span className="hidden sm:inline ml-1">Admin</span>
                </Button>
              )}
              <Button size="sm" onClick={() => navigate("/profile")} variant="outline" className="border-white/20 text-white hover:bg-white/10 h-8 px-2 text-sm">
                <User className="w-4 h-4" />
                <span className="hidden sm:inline ml-1">Perfil</span>
              </Button>
              <span className="hidden md:inline text-sm text-white/80 px-2">{user.email}</span>
              <Button size="sm" onClick={logout} variant="outline" className="border-white/20 text-white hover:bg-white/10 h-8 px-2 text-sm">
                <LogOut className="w-4 h-4" />
                <span className="hidden sm:inline ml-1">Salir</span>
              </Button>
            </>
          ) : (
            <Button size="sm" onClick={() => onRequestAuth && onRequestAuth(true)} className="bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600 h-8 px-3 text-sm">
              <LogIn className="w-4 h-4" />
              <span className="ml-1">Entrar</span>
            </Button>
          )}
        </div>
      </div>
    </header>
  );
};

export default Header;
