import React, { useState, useEffect, useContext } from "react";
import axios from "axios";
import { setCache, getCache } from "@/lib/cache";
import { AuthContext } from "@/App";
import { Input } from "@/components/ui/input";
import { Link, useNavigate } from "react-router-dom";
import ContentCard from "@/components/ContentCard";
import Carousel from "@/components/Carousel";

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL || "http://localhost:8000";
const API = `${BACKEND_URL}/api`;

const HomePage = () => {
  const { user } = useContext(AuthContext) || {};
  const [content, setContent] = useState([]);
  const [filteredContent, setFilteredContent] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    fetchContent();
  }, []);

  useEffect(() => {
    if (searchQuery) {
      const filtered = content.filter((item) => item.title.toLowerCase().includes(searchQuery.toLowerCase()));
      setFilteredContent(filtered);
    } else {
      setFilteredContent(content);
    }
  }, [searchQuery, content]);

  const fetchContent = async () => {
    try {
      const response = await axios.get(`${API}/content`);
      const data = response.data || [];
      setContent(data);
      setFilteredContent(data);
      setCache("content", data);
    } catch (err) {
      console.error(err);
      try {
        const cached = getCache("content");
        if (cached) {
          setContent(cached);
          setFilteredContent(cached);
        }
      } catch {}
    }
  };

  const movies = filteredContent.filter((c) => c.content_type === "movie");
  const tvShows = filteredContent.filter((c) => c.content_type === "tv");
  const novelas = filteredContent.filter((c) => c.content_type === "novela");
  const latest = [...filteredContent].sort((a, b) => new Date(b.created_at) - new Date(a.created_at)).slice(0, 10);

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900">
      <section className="pt-32 pb-16 px-4">
        <div className="container mx-auto text-center">
          <div className="mb-8 flex flex-col items-center justify-center gap-4 bg-white/5 p-6 rounded-xl shadow-lg">
            <div className="flex flex-col md:flex-row items-center gap-4 w-full justify-center">
              <Input
                type="text"
                placeholder="Buscar por ttulo..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="w-64 bg-white/10 border-white/20 text-white placeholder:text-gray-400"
              />
            </div>
          </div>

          <h2 className="text-5xl sm:text-6xl lg:text-7xl font-bold mb-6 fade-in">
            Descubre el Mejor
            <br />
            <span className="bg-gradient-to-r from-cyan-400 via-blue-400 to-purple-400 bg-clip-text text-transparent">Entretenimiento</span>
          </h2>
          <p className="text-lg text-gray-300 max-w-2xl mx-auto fade-in">Pelculas, series y novelas en un solo lugar. Explora, descubre y comparte tu opinin.</p>

          <div className="mt-8 flex items-center justify-center gap-4">
            <Link to="/category/movie" className="px-4 py-2 bg-cyan-600 rounded-md text-white">Pelculas</Link>
            <Link to="/category/tv" className="px-4 py-2 bg-blue-600 rounded-md text-white">Series</Link>
            <Link to="/category/novela" className="px-4 py-2 bg-purple-600 rounded-md text-white">Novelas</Link>
          </div>
        </div>
      </section>

      <section className="px-4 pb-16">
        <div className="container mx-auto">
          {latest.length > 0 && (
            <div className="mb-8">
              <h3 className="text-2xl font-bold mb-4">ltimos agregados</h3>
              <Carousel>
                {latest.map((item) => (
                  <div key={item.id} className="min-w-[200px] sm:min-w-[240px] lg:min-w-[300px]">
                    <ContentCard content={item} navigate={navigate} />
                  </div>
                ))}
              </Carousel>
            </div>
          )}

          {movies.length > 0 && (
            <div className="mb-16">
              <h3 className="text-3xl font-bold mb-6 text-cyan-400">Pelculas</h3>
              <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-6">
                {movies.map((item) => (
                  <ContentCard key={item.id} content={item} navigate={navigate} />
                ))}
              </div>
            </div>
          )}

          {tvShows.length > 0 && (
            <div className="mb-16">
              <h3 className="text-3xl font-bold mb-6 text-blue-400">Series</h3>
              <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-6">
                {tvShows.map((item) => (
                  <ContentCard key={item.id} content={item} navigate={navigate} />
                ))}
              </div>
            </div>
          )}

          {novelas.length > 0 && (
            <div className="mb-16">
              <h3 className="text-3xl font-bold mb-6 text-purple-400">Novelas</h3>
              <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-6">
                {novelas.map((item) => (
                  <ContentCard key={item.id} content={item} navigate={navigate} />
                ))}
              </div>
            </div>
          )}

          {filteredContent.length === 0 && (
            <div className="text-center py-16">
              <p className="text-gray-400 text-xl">No se encontr contenido</p>
            </div>
          )}
        </div>
      </section>
    </div>
  );
};

export default HomePage;