import React, { useRef, useEffect } from "react";
import { ChevronLeft, ChevronRight } from "lucide-react";

// speed: pixels per second
const Carousel = ({ children, speed = 20 }) => {
  const ref = useRef(null);
  const hovering = useRef(false);

  // Convert children to stable arrays and prepare duplicated set for seamless loop
  const items = React.Children.toArray(children).map((c, i) => (
    <div className="carousel-item" key={`item-${i}`}>{c}</div>
  ));

  const scrollAmount = () => {
    const el = ref.current;
    if (!el) return 0;
    return Math.max(el.clientWidth * 0.8, 200);
  };

  const scrollBy = (dir = "next") => {
    const el = ref.current;
    if (!el) return;
    const amount = scrollAmount();
    el.scrollBy({ left: dir === "next" ? amount : -amount, behavior: "smooth" });
  };

  useEffect(() => {
    const el = ref.current;
    if (!el) return;

    let rafId = null;
    let lastTs = null;

    const step = (ts) => {
      if (!lastTs) lastTs = ts;
      const dt = ts - lastTs;
      lastTs = ts;

      if (!hovering.current) {
        // advance by speed * dt
        el.scrollLeft += (speed * dt) / 1000;

        // when we've scrolled through the original content (we duplicated it), wrap back
        const singleWidth = el.scrollWidth / 2; // because we render items twice
        if (el.scrollLeft >= singleWidth) {
          // jump back by singleWidth to create seamless loop
          el.scrollLeft -= singleWidth;
        }
      }

      rafId = requestAnimationFrame(step);
    };

    rafId = requestAnimationFrame(step);

    return () => {
      if (rafId) cancelAnimationFrame(rafId);
    };
  }, [speed, children]);

  return (
    <div
      className="relative"
      onMouseEnter={() => (hovering.current = true)}
      onMouseLeave={() => (hovering.current = false)}
    >
      <button
        aria-label="previous"
        onClick={() => scrollBy("prev")}
        className="absolute left-0 top-1/2 -translate-y-1/2 z-20 bg-black/40 text-white p-2 rounded-full hover:bg-black/60 hidden sm:inline-flex"
      >
        <ChevronLeft className="w-4 h-4" />
      </button>

      <div
        ref={ref}
        tabIndex={0}
        onFocus={() => (hovering.current = true)}
        onBlur={() => (hovering.current = false)}
        className="flex space-x-4 overflow-x-auto no-scrollbar scroll-smooth py-2"
      >
        {items}
        {items.map((child, i) => (
          <div className="carousel-item" aria-hidden="true" key={`dup-${i}`}>
            {React.cloneElement(child.props.children ? child.props.children : child)}
          </div>
        ))}
      </div>

      <button
        aria-label="next"
        onClick={() => scrollBy("next")}
        className="absolute right-0 top-1/2 -translate-y-1/2 z-20 bg-black/40 text-white p-2 rounded-full hover:bg-black/60 hidden sm:inline-flex"
      >
        <ChevronRight className="w-4 h-4" />
      </button>
    </div>
  );
};

export default Carousel;
