import React from "react";

const TMDB_IMAGE_BASE = "https://image.tmdb.org/t/p/w500";

const ContentCard = ({ content, navigate }) => {
  const posterUrl = content.poster_path ? `${TMDB_IMAGE_BASE}${content.poster_path}` : "https://via.placeholder.com/500x750?text=No+Image";

  return (
    <div
      data-testid={`content-card-${content.id}`}
      onClick={() => navigate && navigate(`/content/${content.id}`)}
      className="glass rounded-xl overflow-hidden cursor-pointer card-hover group"
    >
      <div className="relative aspect-[2/3] overflow-hidden">
        <img
          src={posterUrl}
          alt={content.title}
          className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-500"
        />
        <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
      </div>
      <div className="p-4">
        <h4 className="font-semibold text-white truncate">{content.title}</h4>
        <div className="flex items-center justify-between mt-2">
          <span className="text-xs text-gray-400">{content.content_type}</span>
          {content.vote_average && (
            <span className="text-xs text-yellow-400"> {content.vote_average.toFixed(1)}</span>
          )}
        </div>
      </div>
    </div>
  );
};

export default ContentCard;
