import React, { useState, useContext } from "react";
import axios from "axios";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { toast } from "sonner";
import { AuthContext } from "@/App";

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL || "http://localhost:8000";
const API = `${BACKEND_URL}/api`;

const AuthModal = ({ open, onOpenChange }) => {
  const { login } = useContext(AuthContext) || {};
  const [authEmail, setAuthEmail] = useState("");
  const [authPassword, setAuthPassword] = useState("");

  const handleAuth = async (isLogin) => {
    try {
      const endpoint = isLogin ? "/auth/login" : "/auth/register";
      const response = await axios.post(`${API}${endpoint}`, {
        email: authEmail,
        password: authPassword,
      });

      login && login(response.data.access_token, response.data.user);
      onOpenChange && onOpenChange(false);
      setAuthEmail("");
      setAuthPassword("");
      toast.success(isLogin ? "Inicio de sesin exitoso" : "Registro exitoso");
    } catch (error) {
      toast.error(error.response?.data?.detail || "Error de autenticacin");
    }
  };

  return (
    <Dialog open={!!open} onOpenChange={onOpenChange}>
      <DialogContent className="bg-slate-900 border-white/10 text-white">
        <DialogHeader>
          <DialogTitle className="text-2xl font-bold text-center">Bienvenido a FastCopy</DialogTitle>
        </DialogHeader>
        <Tabs defaultValue="login" className="w-full">
          <TabsList className="grid w-full grid-cols-2 bg-white/5">
            <TabsTrigger value="login" data-testid="login-tab">Iniciar Sesin</TabsTrigger>
            <TabsTrigger value="register" data-testid="register-tab">Registrarse</TabsTrigger>
          </TabsList>
          <TabsContent value="login" className="space-y-4">
            <Input data-testid="login-email-input" type="email" placeholder="Email" value={authEmail} onChange={(e) => setAuthEmail(e.target.value)} className="bg-white/5 border-white/10 text-white" />
            <Input data-testid="login-password-input" type="password" placeholder="Contrasea" value={authPassword} onChange={(e) => setAuthPassword(e.target.value)} className="bg-white/5 border-white/10 text-white" />
            <Button data-testid="login-submit-button" onClick={() => handleAuth(true)} className="w-full bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600">Iniciar Sesin</Button>
          </TabsContent>
          <TabsContent value="register" className="space-y-4">
            <Input data-testid="register-email-input" type="email" placeholder="Email" value={authEmail} onChange={(e) => setAuthEmail(e.target.value)} className="bg-white/5 border-white/10 text-white" />
            <Input data-testid="register-password-input" type="password" placeholder="Contrasea" value={authPassword} onChange={(e) => setAuthPassword(e.target.value)} className="bg-white/5 border-white/10 text-white" />
            <Button data-testid="register-submit-button" onClick={() => handleAuth(false)} className="w-full bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600">Registrarse</Button>
          </TabsContent>
        </Tabs>
      </DialogContent>
    </Dialog>
  );
};

export default AuthModal;
